body {
    background: #11998e;  /* fallback for old browsers */
background: -webkit-linear-gradient(to bottom, #38ef7d, #11998e);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to bottom, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
min-width: 100%;
width:100%;
height: auto;
}
.section {
    position: relative;
	width: 100%;
	display: block;
}
.full-height {
    height: 100vh;
}
.over-hide {
    overflow: hidden;
}
.absolute-center {
	position: absolute;
	top: 30%;
	left: 0;
	width: 100%;
  margin-top: 40px;
	transform: translateY(-50%);
	z-index: 2;
}

.input-group-text{
    color:white;
    width:100%;
}
.bg-input-green {
    background: #11998e;
    border-color: #11998e;
}

.bg-input-green::placeholder{
    color:white;
}
.bg-input-green:focus{
    box-shadow: 0 0 5px #11998e;
    border-color: #11998e;;
}
.totalCur::placeholder{
    color:white;
}
.color-green {
    color: #11998e !important;
}
.bg-input-white {   
     background: white !important;
        border-color: white !important;
}
.btn-cambio {
    background: #11998e;  /* fallback for old browsers */
background: -webkit-linear-gradient(to bottom, #38ef7d, #11998e);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to bottom, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
border:none;
color:white;
border-radius: 0;
}
input::after {
    color:white !important;
}
.w-100 {
    width:100%;
}
@media (max-width:500px) {
    .ahoracambio {
        display: block;
    }
}

@media (max-width:400px) {
    .ahoracambio {
        display: none;
    }
}